import cuCustom from '@/colorui/components/cu-custom.vue'
import asyncSwitch from "@/components/helang-asyncSwitch/helang-asyncSwitch.vue"
import mpopup from '../../../../components/xuan-popup/xuan-popup.vue'
export default {
	data() {
		return {
			// 收货地址信息
			siteForm: {
				users_name:'',
				users_mobile:'',
				dormitory:'',
				dormitory_no:'',
				address:''
				
			},
			checked1:true,
			// siteForm:{},
			siteId : 0,
			previousPage:''
		}
	},
	methods: {	
		change1() {
			if(this.checked1) {
				this.checked1 = false
			} else {
				this.checked1 = true
			}
			
			console.log(this.checked1)
			},
		formData() {
			// console.log(this.siteId)
			const that = this
			uni.getStorage({
			    key: 'id',
			    success: function (res) {
					that.siteId = res.data	
					// console.log(that.siteId)
			    },
				
			});
			
		},
		// 确认修改
		alertSite() {
			if(this.siteForm.users_name == '') {
				this.$refs.mpopup.open({
				    type:'err',
				    content:'收货人姓名不能为空',
				    timeout:1000,
				    isClick:false
				});
				return
			} else if(this.siteForm.users_mobile == '' || parseInt(this.siteForm.users_mobile) != this.siteForm.users_mobile) {
				this.$refs.mpopup.open({
				    type:'err',
				    content:'收货人电话不能为空并且必须是数字',
				    timeout:1000,
				    isClick:false
				});
				return
			} else if(this.siteForm.dormitory == '') {
				this.$refs.mpopup.open({
				    type:'err',
				    content:'收货人宿舍楼不能为空',
				    timeout:1000,
				    isClick:false
				});
				return
			} else if(this.siteForm.dormitory_no == '') {
				this.$refs.mpopup.open({
				    type:'err',
				    content:'收货人宿舍号不能为空',
				    timeout:1000,
				    isClick:false
				});
				return
			}
			let id = uni.getStorageSync('user_id')
			uni.request({
				// 接口
				url: 'http://47.104.29.236:9999/api/v1/alertSite',
				// 请求方式
				method: 'GET',
				// 携带的数据
				data: {msg:this.siteForm,checked1:this.checked1,id:id},
				// 请求成功
				success: res => {
					// console.log(res)
					if(res.data.ok == 1) {
						this.$refs.mpopup.open({
						    type:'success',
						    content:'修改地址成功',
						    timeout:2000,
						});
						setTimeout(()=> {
							// 返回地址页面
							uni.navigateTo({
								url:'../../xy-site/site',
								success:res =>{},
								fail:()=>{},
								complete:()=>{}
							})
						},2000)
					}
					// 返回地址页面
					// uni.navigateTo({
					// 	url:'../../xy-site/site',
					// 	success:res =>{},
					// 	fail:()=>{},
					// 	complete:()=>{}
					// })
				},
				
				// 请求失败
				fail: () => {},
				complete: () => {}
			})
			
		},
		// 删除功能
		deleteSite() {
		
			uni.request({
				// 接口
				url: 'http://47.104.29.236:9999/api/v1/deleteSite',
				// 请求方式
				method: 'DELETE',
				// 携带的数据
				data: {id:this.siteId},
				// 请求成功
				success: res => {
					console.log(res)
					if(res.data.ok == 1) {
						this.$refs.mpopup.open({
						    type:'success',
						    content:'删除地址成功',
						    timeout:2000,
						});
						setTimeout(()=> {
							// 返回地址页面
							uni.navigateTo({
								url:'../../xy-site/site',
								success:res =>{},
								fail:()=>{},
								complete:()=>{}
							})
						},2000)
					}
					
				},
				
				// 请求失败
				fail: () => {},
				complete: () => {}
			})
			
		},
		alertForm() {
			uni.request({
				// 接口
				url: 'http://47.104.29.236:9999/api/v1/siteShow',
				// 请求方式
				method: 'GET',
				// 携带的数据
				data: {id:this.siteId},
				// 请求成功
				success: res => {
					this.siteForm = res.data.data[0]
					// console.log(res)
					// // this.$set('siteForm',res.data.data[0])
					// console.log(this.siteForm)
					if(res.data.data[0].sdefault === 1) {
						this.checked1 = true
					} else {
						this.checked1 = false
					}
				},
				// 请求失败
				fail: () => {},
				complete: () => {}
			})
		}
	},
	components: {
		cuCustom,
		"async-switch":asyncSwitch,
		mpopup
	},
	onShow(){
	},
	onLoad(e) {
		this.previousPage = e
		this.siteId = e.id 
		this.formData()
		// console.log(this.siteForm)
		this.alertForm()
	},
	
}
